  color: #3c4043;
}
.page-menu.no-border {
  border: 0;
}
.page-menu > .container {
  position: relative;
}
.page-menu .menu-title {
  display: none;
}
.page-menu nav {
  background-color: #fff;
  text-align: center;
}
.page-menu nav > ul {
  list-style: none;
  padding: 0;
  margin: 0;
  position: relative;
}
.page-menu nav > ul > li {
  transition: all 0.3s ease;
  position: relative;
  border: 0;
  display: inline-block;
  float: none;
  padding: 0px 2px;
  font-family: "Poppins", sans-serif;
}
.page-menu nav > ul > li:last-child {
  margin-right: 0;
}
.page-menu nav > ul > li > a {
  position: relative;
  padding: 6px 16px;
  font-size: 13px;
  line-height: 12px;
  color: #3c4043;
  border-radius: 50px;
  transition: all 0.2s ease;
  border-width: 2px;
  border-style: solid;
  border-color: transparent;
  font-weight: 500;
}
.page-menu nav > ul > li > a:after, .page-menu nav > ul > li > a:before {
  transition: all 0.3s ease;
}
.page-menu nav > ul > li:hover > a, .page-menu nav > ul > li.active > a {
  color: #2250fc;
}
.page-menu nav > ul > li.dropdown {
  line-height: 50px;
}
.page-menu nav > ul > li.dropdown a {
  padding-right: 20px;
}
.page-menu nav > ul > li.dropdown:before {
  font-size: 12px;
  transition: all 0.2s ease;
  content: "\e92e";
  font-family: "inspiro-icons";
  right: 10px;
  top: 1px;
  position: absolute;
  color: #3c4043;
  z-index: 2;
}
.page-menu nav > ul > li .dropdown-menu {
  position: absolute;
  text-align: left;
  list-style: none;
  font-family: "Poppins", sans-serif;
  padding: 14px 20px;
  width: auto;
  top: 50px;
  left: 0;
  margin: 0;
  border-radius: 4px;
  background-color: #fff;
  border: 1px solid #e4e6ef;
  min-width: 180px;
  box-shadow: 0 14px 20px rgba(0, 0, 0, 0.1);
}
.page-menu nav > ul > li .dropdown-menu > li {
  width: 100%;
}
.page-menu nav > ul > li .dropdown-menu > li a,
.page-menu nav > ul > li .dropdown-menu > li > span {
  line-height: 26px;
  white-space: nowrap;
  display: block;
  padding: 4px 10px;
  font-size: 13px;
  font-weight: 400;
}
.page-menu nav > ul > li .dropdown-menu.menu-last {
  right: 0;
  left: auto;
}
.page-menu nav > ul > li:last-child {
  margin-right: 0;
