.lines-button {
  appearance: none;
  border: none;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  transition: 0.3s;
  user-select: none;
  width: 16px;
  background: transparent;
  outline: none;
}
.lines-button > * {
  display: block;
}
.lines-button:hover, .lines-button:focus {
  outline: none;
}
.lines-button::-moz-focus-inner {
  border: 0;
}

.lines {
  display: inline-block;
  height: 2px;
  width: 20px;
  border-radius: 4px;
  transition: 0.3s;
  position: relative;
  top: -4px;
}
.lines::before, .lines::after {
  display: inline-block;
  height: 2px;
  width: 20px;
  border-radius: 4px;
  transition: 0.3s;
  content: "";
  position: absolute;
  left: 0;
  transform-origin: 2.85714px center;
  width: 100%;
}
.lines::before {
  top: 6px;
}
.lines::after {
  top: -6px;
}
.lines, .lines:before, .lines:after {
  background-color: #111;
}

.toggle-active .lines {
  -webkit-transform: scale3d(0.8, 0.8, 0.8);
  transform: scale3d(0.8, 0.8, 0.8);
}

.x {
  width: auto;
}

.x.toggle-active .lines {
  background: transparent !important;
}

.x.toggle-active .lines::before,
.x.toggle-active .lines::after {
  transform-origin: 50% 50%;
  top: 0;
  width: 26px;
}

.x.toggle-active .lines::before {
  transform: rotate3d(0, 0, 1, 45deg);
}

.x.toggle-active .lines::after {
  transform: rotate3d(0, 0, 1, -45deg);
}

.toggle-item i {
  position: relative;
}
.toggle-item i:nth-of-type(2) {
  visibility: hidden;
  position: absolute;
}
.toggle-item.toggle-active i:nth-of-type(2) {
  visibility: visible;
  position: relative;
}
.toggle-item.toggle-active i:nth-of-type(1) {
  visibility: hidden;
  position: absolute;
}

