/* ----------------------------------------------------------------
	Accordion
-----------------------------------------------------------------*/
.accordion .ac-item {
  margin-bottom: 10px;
  padding: 12px 20px;
  border-radius: 5px;
  border: 1px solid #e4e6ef;
}
.accordion .ac-item .ac-title {
  font-weight: 500;
  position: relative;
  padding-right: 10px;
  margin: 0;
  font-size: 1em;
  cursor: pointer;
  user-select: none;
}
.accordion .ac-item .ac-title:before {
  font-size: 18px;
  content: "\e9be";
  font-family: "inspiro-icons";
  right: 0;
  position: absolute;
}
.accordion .ac-item .ac-title > i {
  margin-right: 8px;
}
.accordion .ac-item .ac-content {
  padding-top: 12px;
}
.accordion .ac-item .ac-content > p:last-child {
  margin-bottom: 6px;
}
.accordion .ac-item.ac-active .ac-title:before {
  content: "\e9a1";
}
.accordion.accordion-shadow .ac-item {
  box-shadow: 0 6px 12px rgba(140, 152, 164, 0.075);
}
.accordion.accordion-simple .ac-item {
  padding: 14px 0;
  margin-bottom: 0;
  border-radius: 0;
  border: 0;
  border-bottom: 1px solid #e4e6ef;
}
.accordion.accordion-simple .ac-item:last-child {
  border: 0;
}
.accordion.accordion-flat .ac-item {
  padding: 10px 0;
  margin-bottom: 0;
  border-radius: 0;
  border: 0;
}
.accordion.accordion-theme .ac-item {
  background-color: #2250fc;
  border: 0;
}
.accordion.accordion-theme .ac-item .ac-title {
  color: #fff;
}
.accordion.accordion-theme .ac-item .ac-content,
.accordion.accordion-theme .ac-item .ac-content p {
  color: #fff;
}
.accordion.dark .ac-item {
  background-color: #1e2022;
  border: 0;
}
.accordion.dark .ac-item .ac-title {
  color: #fff;
}
.accordion.dark .ac-item .ac-content,
.accordion.dark .ac-item .ac-content p {
  color: #fff;
}

/* ----------------------------------------------------------------
	Alert
-----------------------------------------------------------------*/
.alert {
  color: #fff;
  padding: 0.85rem 1.5rem;
  z-index: 9999;
}
.alert span[data-notify~=title] {
  display: block;
  font-weight: bold;
}
.alert span.fas {
  font-size: 1rem;
  float: left;
  padding-top: 10px;
  min-width: 40px;
  min-height: 40px;
}
.alert .close {
  color: #fff;
  font-weight: 300;
  padding: 12px 18px;
}
.alert > .alert-icon {
  float: left;
}
.alert > .alert-icon i {
  font-size: 34px;
}
.alert > p {
  padding-left: 54px;
}
.alert.alert-primary {
  background-color: #2250fc;
  border-color: rgb(8.8415178571, 60.078125, 251.6584821429);
}
.alert.alert-secondary {
  background-color: #c1c3ce;
  border-color: rgb(178.7567567568, 181.2162162162, 194.7432432432);
}
.alert.alert-success {
  background-color: #28a745;
  border-color: rgb(35.0724637681, 146.4275362319, 60.5);
}
.alert.alert-info {
  background-color: #17a2b8;
  border-color: rgb(20.1666666667, 142.0434782609, 161.3333333333);
}
.alert.alert-danger {
  background-color: #dc3545;
  border-color: rgb(210.9493670886, 36.5506329114, 53.2594936709);
}
.alert.alert-warning {
  background-color: #ffc107;
  border-color: rgb(236.5, 177.375, 0);
}
.alert.alert-light {
