  height: 16px;
  border-radius: 50%;
  background-color: #2250fc;
  position: absolute;
  bottom: 0;
  left: 0;
  margin: auto;
  transform: scale(0.5);
  transform-origin: center center;
  animation: pulse-me 3s linear infinite;
}
